import {Component} from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams} from 'ionic-angular';
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {TokenServiceProvider} from "../../providers/token-service/token-service";
import {CashByCodeServiceProvider} from "../../providers/cash-by-code-service/cash-by-code-service";

@IonicPage()
@Component({
  selector: 'page-cash-by-code02',
  templateUrl: 'cash-by-code02.html',
})
export class CashByCode02Page {
  cashByCodeError02;

  accountNo;
  transferDetails01;
  srcAccDetails;
  showMobile = false;
  showNameNID=false;
  formVal = {amount: "", benType: "", benAccount: "", benName: "", benNID: ""};
  amount;
  benAccount;
  benName;
  benType;
  nid;
  beneficiaryList;
  selectedAccount;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public cryptoUtil: CryptoParamUtilProvider,
              public maskTokenUtil: MaskTokenProvider,
              public storage: LocalStorageProvider,
              public alertCtrl: AlertController,
              public cashByCodeService: CashByCodeServiceProvider,
              public tokenService: TokenServiceProvider) {

    this.accountNo = this.navParams.get("accountNo");
    let txDetail=this.navParams.get("transferDetails01");
    this.transferDetails01 = JSON.parse(txDetail);
    this.srcAccDetails = this.transferDetails01.accInfoBean;
    let srPage = this.navParams.get("sourcePage");
    if (srPage == "listPage") {
      let selAccount = this.navParams.get("selectedAccount");
      this.selectedAccount = JSON.parse(selAccount);
      let amount = this.navParams.get("amount");
      this.formVal = {
        amount: amount, benType: "O", benAccount: this.selectedAccount.subscriptionValue,
        benName: this.selectedAccount.subscriptionDesc, benNID: this.selectedAccount.additionalInfo
      };
      this.showMobile=true;
      this.showNameNID=true;
    }

    else {
      this.formVal = {amount: "500", benType: "", benAccount: "", benName: "", benNID: ""};
      this.showMobile = false;
      this.showNameNID=false;
    }


  }

  onSelectChange(selectedValue) {
    if (selectedValue == "O") {
      this.showMobile = true;
      this.showNameNID=false;
    }
    else {
      this.showMobile = false;
      this.showNameNID=false;
      this.formVal.benAccount = "";
      this.formVal.benName = "";
      this.formVal.benNID = "";
    }
  }


  CashByCode02Action = function () {
    this.cashByCodeError02 = "";

    let validateAmountExp = new RegExp("[0-9]{1,6}$");
    let flag = true;
    this.amount = this.formVal.amount;
    this.benAccount = this.formVal.benAccount;
    this.benName = this.formVal.benName;
    this.benType = this.formVal.benType;
    this.nid= this.formVal.benNID;


    if (this.benType == "" || typeof(this.benType) == "undefined") {
      this.cashByCodeError02 = "Please Select Beneficiary Type.";
      flag = false;
      return;
    }

    if (this.benType == "O") {
      if (this.benAccount == "" || typeof(this.benAccount) == "undefined") {
        this.cashByCodeError02 = "Please Enter Beneficiary Mobile No.";
        flag = false;
        return;
      }

      if (this.benName == "" || typeof(this.benName) == "undefined") {
        this.cashByCodeError02 = "Please Enter Beneficiary Name.";
        flag = false;
        return;
      }

    }

    if (this.amount == "" || typeof(this.amount) == "undefined") {
      this.cashByCodeError02 = "Please Enter Transfer Account.";
      flag = false;
      return;
    }

    if (!validateAmountExp.test(this.amount)) {
      this.cashByCodeError02 = "Invalid Transfer amount.";
      flag = false;
      this.formVal = {amount: ""};
      return;
    }


    if (flag) {
      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
      this.tokenService.getNewToken(unMaskRefToken)
        .then(data => {
          this.onNewTokenGenComplete(data);
        });
    }
  };

  onNewTokenGenComplete = function (data) {

    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let appData = this.storage.retrieveFromStorage("appData");
      let encSrcAccNo = this.cryptoUtil.getEncryptVal(this.accountNo);
      let encBenType = this.cryptoUtil.getEncryptVal(this.benType);
      let encAmount = this.cryptoUtil.getEncryptVal(this.amount.toString());
      let encBenAccount = this.cryptoUtil.getEncryptVal(this.benAccount);
      let encName = this.cryptoUtil.getEncryptVal(this.benName);
      let encNID = this.cryptoUtil.getEncryptVal(this.nid);
      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      this.cashByCodeService.initCashRemittance(unMaskToken, appData.userId, appData.appId, encSrcAccNo, encBenType, encBenAccount, encName, encNID, encAmount)
        .then(data => {
          this.onInitCashRemitComplete(data);
        });
    }
  };

  onInitCashRemitComplete = function (data) {

    if (data.statusCode == 1) {
      this.navCtrl.push('CashByCode03Page', {
        'srcAccDetails': JSON.stringify(this.srcAccDetails),
        'initCashByCodeResponse': JSON.stringify(data.object),
        'nid' : this.formVal.benNID
      });
    }

    else if (data.statusCode == 0) {
      this.cashByCodeError02 = data.note;
    }

    else {
      this.cashByCodeError02 = "System Error Contact with Administrator.";
    }
  };

  selectDestinationAccount = function () {
    let appData = this.storage.retrieveFromStorage("appData");
    let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
    this.tokenService.getNewToken(unMaskRefToken)
      .then(data => {
        this.onNewTokenGenCompleteForFetch(data);
      });
  };

  onNewTokenGenCompleteForFetch = function (data) {

    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      this.cashByCodeService.fetchCashByCodeBeneficiary(unMaskToken, appData.userId, appData.appId)
        .then(data => {
          this.onFetchCashByCodeAccountsListComplete(data);
        });
    }
  };

  onFetchCashByCodeAccountsListComplete = function (data) {
    if (data.statusCode == 1) {
      let creditAccDetails = data.object;
      this.beneficiaryList = creditAccDetails.beans;
      let listSize = this.beneficiaryList.length;
      if (listSize <= 0) {
        this.cashByCodeError02 = "You have no beneficiary subscription. Please subscribe first.";
        return;
      }
      else {

        this.navCtrl.push('CashByCodeBeneficiarySelectionPage', {
          'accountNo': this.accountNo,
          'beneficiaryList': JSON.stringify(this.beneficiaryList),
          'transferDetails01': JSON.stringify(this.transferDetails01),
          'amount': this.formVal.amount
        })
      }
    }

    else if (data.statusCode == 0) {
      this.cashByCodeError02 = data.note;
    }

    else {
      this.cashByCodeError02 = "System Error Contact with Administrator.";
    }
  };


  ionViewDidLoad() {
  }

}



